.search-header {
  height: 1rem;
  background: #212021;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.search-header input {
  width: 4.3rem;
  height: 0.62rem;
  border: 0;
  outline: none;
  border-radius: 4px;
  margin-top: 0.15rem;
  margin-left: 0.2rem;
  padding-left: 0.6rem;
  background: url(../../static/img/ss.1f8d134.jpg) no-repeat center left;
  background-color: #fff;
  background-size: 8%;
  font-size: 0.24rem;
}
.search-header a {
  color: #fff;
  margin-left: 0.2rem;
  font-size: 0.26rem;
}
.search-con1 {
  height: 1.77rem;
  padding-left: 0.2rem;
  position: relative;
  top: 1rem;
}
.search-con1 h4 {
  line-height: 0.8rem;
  font-size: 0.28rem;
  color: #a5a6a5;
}
.search-con1 ul {
  height: 0.9rem;
  position: relative;
}
.search-con1 ul li {
  position: absolute;
  line-height: 0.47rem;
  width: 0.9rem;
  text-align: center;
  /* margin-right:0.3rem; */
  font-size: 0.24rem;
  border-radius: 9px;
  z-index: 90;
}
.search-con1 ul .showcolor {
  color: #fff;
}
.search-con1 #moveColor {
  width: 0.8rem;
  height: 0.47rem;
  position: absolute;
  left: 0.7rem;
  top: 0.8rem;
  background: #ff5d00;
  border-radius: 9px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.change-content {
  margin-top: 1rem;
  width: 100%;
  min-height: 0.3rem;
  overflow: hidden;
}
.change-content > li {
  width: 100%;
  min-height: 0.2rem;
  overflow: hidden;
  display: none;
}
.change-content > li > .rank {
  width: 100%;
  height: 4.9rem;
  padding-left: 0.1rem;
}
.change-content > li > .rank li {
  float: left;
  width: 2rem;
  margin-right: 0.1rem;
}
.change-content > li > .rank li h3 {
  height: 0.4rem;
  width: 100%;
  text-align: center;
  color: #fff;
  line-height: 0.4rem;
  font-size: 0.24rem;
}
.change-content > li > .rank li img {
  width: 100%;
  height: 3.30rem;
}
.change-content > li > .rank li p {
  font-size: 0.24rem;
  padding-left: 0.1rem;
  margin-top: 0.1rem;
}
.change-content > li .after-rank {
  min-height: 2rem;
  overflow: hidden;
}
.change-content > li .after-rank li {
  height: 0.9rem;
  border-top: 1px solid #e1e1e1;
  line-height: 0.9rem;
  padding: 0 0.1rem;
}
.change-content > li .after-rank li a{
  display:block;
  width:100%;
  height:100%;
}
.change-content > li .after-rank li a b {
  float: left;
  font-size: 0.24rem;
  font-weight: 100;
  color: #000;
}
.change-content > li .after-rank li a b span {
  color: #8b8b8b;
}
.change-content > li .after-rank li a b i {
  font-style: inherit;
}
.change-content > li .after-rank li a > span {
  float: right;
  font-size: 0.22rem;
  color: #888;
  font-family: iconfont;
}
.change-content .showli {
  display: block;
}
